.beauty-container {
    max-width: 1920px;
    margin: 0 auto; 
    &__inner {
        margin: 0 auto; 
        width: 1800px; 
        @media (max-width: 1919px) {
            width: 1560px; 
        }
    }
    &__title {
        padding: 47px 0 50px 0;
        text-align: center;
        @include font(44px, 200, #000, 54px);
    }
    &__banner {
        margin-bottom: 20px; 
    }
    &__cate-link {
        display: flex; 
        width: 100%;
    }
    &__cate-link-list {
        margin-bottom: 80px;
        display: flex; 
    }
    &__cate-link-item {
        margin-right: 20px; 
        width: 100%;
        &:last-child {
            margin-right: 0;
        }
        a {
            @include flex(row, center, center); 
            padding: 0 10px; 
            cursor: pointer;
            width: 100%; 
            height: 100px;
            border: 2px solid #000;
            font-size: 26px; 
            &:hover {
                background-color: #000;
                color: #fff; 
            }
        }
    }
    .float-txt {
        position: absolute;
        left: 50%;
        top: 50%; 
        transform: translate(-50%, -50%);
        text-align: center;
        strong {
            @include font(58px, 400, #fff, 1);
        }
        p {
            margin: 20px 0 25px; 
            @include font(16px, 400, #fff, 1);
        }
        em {
            display: inline-block;
            padding: 16px 20px;
            @include font(15px, 400, #fff, 1);
            border: 2px solid #fff ;
        }
    }
    // top banner
    .top-banner-container {
        display: flex;
        justify-content: center;
        margin-bottom: 90px; 
        .left {
            position: relative;
            display: inline-block;
            margin-right: 20px;
            width: 900px;
            height: 452px; 
            background-image: url('../../../../public/images/beauty_section01.jpg');
        }
        .right {
            position: relative;
            display: inline-block;
            width: 640px; 
            height: 452px; 
            background-image: url('../../../../public/images/beauty_section02.jpg');
        }
        
    }
    // multi slide 
    .brands-menu-area {
        margin-bottom: 40px; 
        @include flex(row, center, center);
        button {
            padding: 0 13px 0 14px; 
        }
        .active {
            color: #333; 
            font-weight: 500; 
            font-size: 16px; 
            text-decoration: underline;
        }
        .non-active {
            color: #c0c0c0;
            height: 25px; 
        }
    }
    // make-it-yours-container
    .make-it-yours-container {
        margin-bottom: 100px; 
    }
    // sub-banner-container 
    .sub-banner-container {
        margin-bottom: 100px; 
        ul {
            @include flex(row, center, center);
        }
        li {
            position: relative; 
            width: 100%; 
            img {
                width: 100%; 
            }
            &:first-child {
                margin-left: 0;
            }
            &:nth-child(2) {
                margin-left: 20px;
            }
            &:nth-child(3) {
                margin-left: 20px;
            }
            .float-txt {
                position: absolute; 
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    // top-rank-container
    .top-rank-container {
        display: flex; 
        flex: 1; 
        margin-bottom: 20px; 
        .left {
            width: 271px; 
            border-right: 1px solid #ccc; 
            .list {
                margin-bottom: 80px; 
                h2 {
                    font-size: 23px; 
                    color: #333; 
                    margin-bottom: 20px; 
                }
                li {
                    margin-top: 10px; 
                    @include flex(row, start, center);
                    .num {
                        @include flex(row, center, center);
                        margin-right: 10px; 
                        width: 18px;
                        height: 18px; 
                        background-color: #000;
                        color: #fff; 
                    }
                    .title {
                        @include ellipsis(1);
                        font-size: 14px;
                        color: #333; 
                    }
                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
        }
        .right {
            display: flex;
            padding-left: 50px; 
            width: calc(100% - 271px);
            .top-one-item,
            .top-etc-item
            {
                display: inline-block;
                .img-area {
                    position: relative;
                    margin-bottom: 16px; 
                    .seller-bage-icon {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 24px; 
                        height: 35px; 
                        > img {
                            width: 100%; 
                        }
                        span {
                            position: absolute;
                            top: 5px;
                            left: 50%;
                            transform: translate(-50%, -0%);
                            display: block; 
                            font-size: 18px;
                            color: #fff;
                        }
                    }
                }
                .txt-wrap {
                    margin-bottom: 20px; 
                    height: 65px;
                    .brand {
                        margin-bottom: 8px; 
                        font-size: 14px;
                        @include ellipsis(1);
                    }
                    .product {
                        margin-top: 8px;
                        margin-bottom: 16px; 
                        height: 34px; 
                        font-size: 12px;
                        color: #777;
                        line-height: 16px; 
                    }
                    .price {
                        margin-top: 20px; 
                        @include flex(row, space-between, center);
                        .discount_price {
                            margin-right: 5px;
                            font-size: 16px;
                            color: #333;
                            font-weight: 500;
                        }
                        .base_price {
                            font-size: 12px;
                            color: #aaa;
                            text-decoration: line-through;
                        }
                        .discount_rate {
                            font-size: 16px;
                            color: #ff4000;
                            font-weight: 500;
                        }
                    }
                }
            }
            // top one item 
            .top-one-item {
                width: 477px;
                .img-area {
                    height: 660px; 
                }
                .txt-wrap {
                    height: 163px; 
                }
            }
            // top etc item list 
            .top-etc-item-list {
                display: flex; 
                flex-wrap: wrap;
                .top-etc-item {
                    width: 171px; 
                    margin-left: 19px;
                    a {
                        display: inline-block;
                        width: 100%;
                        .img-area {
                            > img {
                                width: 100%; 
                            }
                        }
                        .txt-wrap {
                            margin-bottom: 0;
                            height: 165px; 
                        }
                    }
                }
            }
        }
    }

    // beauty__brand-container
    .beauty-container {
        &__brand-container {
            margin-top: 0 !important;
            .thumbnail__title {
                margin-bottom: 15px !important; 
                font-size: 38px; 
            }
            .slick-arrow {
                position: absolute;
                z-index: 2;
                top: 50%;
                transform: translate(-50, 0);
                &.slick-prev {
                    left: 2rem;
                }
                &.slick-next {
                    right: 2rem;
                }
            }
            .slick-prev {
                &:before {
                    display: none;
                }
                &:after {
                    width: 100%;
                    display: block; 
                    content: '';
                    height: 56px;
                    background: url('/assets/images/btn_prev.png') no-repeat center center / 100%;
                }
            }
            .slick-next {
                &:before {
                    display: none;
                }
                &:after {
                    display: block;
                    content: '';
                    height: 56px; 
                    background: url('/assets/images/btn_next.png') no-repeat center center / 100%;
                }
            }
            .wrap-img {
                width: 700px; 
                height: 640px;
                object-fit: cover;
            }
            .small-img-container {
                width: 860px;
                height: 640px; 
                .small-img-inner {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 100%; 
                    li {
                        position: relative;
                        margin-right: 22px;
                        &:last-child {
                            margin-right: 0;
                        }
                        .heart-icon {
                            position: absolute;
                        }
                    }
                }
                
            }
            a {
                display: inline-block;
                height: 288px; 
                    .small-img-area {
                        margin-bottom: 16px; 
                        height: 100%; 
                        > img {
                        height: 100%; 
                    }
                    
                }
                .small-txt-area {
                    height: 185px; 
                    .title {
                        margin: 0 0 7px 0;
                        width: auto; 
                        @include ellipsis(1);
                        font-size: 14px;
                        line-height: 16px;
                    }
                    .desc {
                        height: 32px; 
                    }
                    .price-area {
                        margin-top: 25px; 
                        height: 24px; 
                        @include flex(row, space-between, center);  
                        .discount_price {
                            font-size: 16px; 
                            color: #333; 
                            margin-right: 4px; 
                        }
                        .base_price {
                            font-size: 12px; 
                            color: #aaa; 
                            text-decoration: line-through;
                        }
                        .discount_percent {
                            font-size: 16px;
                            color: #ff4000;
                        }
                    }
                }
                
            }
        }
    }
}